'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import Link from 'next/link';

interface Dog {
    id: string;
    name: string;
    image: string;
    breed: string;
    price: number;
}

interface DogsResponse {
    page: number;
    dogs: Dog[];
}

export function DogList() {
    const [dogs, setDogs] = useState<Dog[]>([]);
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        async function fetchDogs() {
            try {
                setLoading(true);
                const apiUrl = 'http://127.0.0.1:5000';
                const response = await fetch(`${apiUrl}/dogs?page=${page}`);

                if (!response.ok) {
                    throw new Error(`Failed to fetch dogs: ${error}`);
                }
                const data: DogsResponse = await response.json();
                setDogs(data.dogs);
            } catch (err) {
                setError(err instanceof Error ? err.message : 'An error occurred');
                console.error('Error fetching dogs:', err);
            } finally {
                setLoading(false);
            }
        }

        fetchDogs();
    }, [page]);

    if (loading && dogs.length === 0) {
        return <div className="text-center">Loading dogs...</div>;
    }

    if (error) {
        return <div className="text-center text-red-500">Error: {error}</div>;
    }

    return (
        <div className="w-full max-w-6xl">
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
                {dogs.map((dog) => (
                    <div key={dog.id} className="border rounded-lg overflow-hidden shadow-md hover:shadow-lg transition-shadow">
                        <div className="relative h-48 w-full">
                            <Image 
                                src={dog.image} 
                                alt={dog.name}
                                fill
                                sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                                className="object-cover"
                            />
                        </div>
                        <div className="p-4">
                            <h3 className="text-lg font-bold">{dog.name}</h3>
                            <p className="text-gray-600">{dog.breed}</p>
                            <p className="text-green-600 font-semibold mt-2">${dog.price}</p>
                            <Link 
                                href={`/dog/${dog.id}`} 
                                className="mt-3 inline-block px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
                            >
                                View Details
                            </Link>
                        </div>
                    </div>
                ))}
            </div>

            {dogs.length > 0 && (
                <div className="flex justify-center mt-8 gap-4">
                    <button 
                        onClick={() => setPage(prev => Math.max(prev - 1, 1))}
                        disabled={page === 1}
                        className="px-4 py-2 border rounded disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                        Previous
                    </button>
                    <span className="px-4 py-2">Page {page}</span>
                    <button 
                        onClick={() => setPage(prev => prev + 1)}
                        disabled={dogs.length < 20}
                        className="px-4 py-2 border rounded disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                        Next
                    </button>
                </div>
            )}
        </div>
    );
}